<template>

    <iframe
        :src="address"
        
        style="
            border: 0;
            margin: 0 auto;
            display: block;
            border-radius: 10px;
            height: 90vh;
            width: 100%;    
            min-width: 300px;
        "
    />

</template>

<script>

    export default {
        name: 'Uniswap',

        computed: {
            address() {
                if (this.$vuetify.theme.dark) {
                    return 'https://app.uniswap.org/#/swap?theme=dark';
                } else {
                    return 'https://app.uniswap.org/#/swap?theme=light';
                }
            }
        }
    }

</script>